/* AboutMe Ï†ÑÏ≤¥ */
.project-section {
  position: relative; /* üî• Í∏∞Ï§Ä ÏòÅÏó≠ ÏßÄÏ†ï */
  height: 100vh;
  scroll-margin-top: -300px;
}

/* Î©îÏù∏ Î∞∞Í≤Ω Ïù¥ÎØ∏ÏßÄ */
.project_main_image {
  position: relative;
  margin: auto;
  display: block;
}

  /* ÌåüÏä§Ìåü */
  .project_overlay_image_1 {
    position: absolute;
    top: 55.1%;
    left: 24.6%;
    width: 19.65%;
    height: auto; /* ÏÑ∏Î°ú ÎπÑÏú® Ïú†ÏßÄ */
    transform: translate(-50%, -50%);
    }

  /* Î©îÎîîÌååÏä§ */
  .project_overlay_image_2 {
    position: absolute;
    top: 55.1%;
    left: 50%;
    width: 19.65%;
    height: auto;
    transform: translate(-50%, -50%);
  }

  /* ÎîîÏΩîÎ≤≥ */
  .project_overlay_image_3 {
    position: absolute;
    top: 55%;
    left: 75.42%;
    width: 19.65%;
    height: auto;
    transform: translate(-50%, -50%);  
  }

  /* Í≥µÌÜµ ÌôïÎåÄ Ìö®Í≥º */
  .project_overlay_image_1,
  .project_overlay_image_2,
  .project_overlay_image_3 {
    transition: transform 0.3s ease;
  }

  .project_overlay_image_1:hover,
  .project_overlay_image_2:hover,
  .project_overlay_image_3:hover {
    transform: translate(-50%, -50%) scale(1.05); /* ÏõêÎûò ÏúÑÏπò Ïú†ÏßÄÌïòÎ©¥ÏÑú ÏÇ¥Ïßù ÌôïÎåÄ */
  }

/* üì± Î™®Î∞îÏùº Ï†ÑÏö© Ïä§ÌÉÄÏùº */
@media (max-width: 768px) {
  /* AboutMe Ï†ÑÏ≤¥ */
  .project-section {
    height: 100vh;
    position: relative;
  }

  /* Î©îÏù∏ Î∞∞Í≤Ω Ïù¥ÎØ∏ÏßÄ */
  .project_main_image {
    width: 100%;
    height: auto;
  }

  /* Î™®Î∞îÏùºÏóêÏÑúÎäî ÏÑ∏Î°úÎ°ú ÏïÑÏù¥ÏΩò Ï†ïÎ†¨ */
  .project_overlay_images {
    position: absolute;
    top: 15%;
    left: 50%;
    transform: translateX(-50%);
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 10px;
  }

  .project_overlay_image_1,
  .project_overlay_image_2,
  .project_overlay_image_3 {
    position: relative;
    top: auto;
    left: auto;
    width: 67%;
    height: auto;
    transform: none;
  }

  /* Î™®Î∞îÏùº hover Ìö®Í≥º Ï†úÍ±∞ (ÌÑ∞ÏπòÎãàÍπå) */
  .project_overlay_image_1:hover,
  .project_overlay_image_2:hover,
  .project_overlay_image_3:hover {
    transform: none;
  }
}  