body {
  overflow-x: hidden;
}

/* Ï†ÑÏ≤¥Î≥¥Îã§ ÏúÑ */
.header-wrapper {
  position: relative;
}

/* Ï†ÑÏ≤¥ */
.header-all {
  display:flex; 
  justify-content: space-between;
  height: 80px;
  top: 0; /* ÏÉÅÎã®Ïóê Í≥†Ï†ï */
  position: sticky; /* Ïä§ÌÅ¨Î°§ ÏãúÏóêÎèÑ Í≥†Ï†ïÎêòÎèÑÎ°ù ÏÑ§Ï†ï */
  z-index: 1000; /* Îã§Î•∏ ÏöîÏÜå ÏúÑÏóê ÌëúÏãúÎêòÎèÑÎ°ù ÏÑ§Ï†ï */
  font-family: 'Freesentation', sans-serif;
  color: #000000af;
  background-color: white;
  padding: 0 20px;
  align-items: center;  
}

/* Î°úÍ≥† Ï†ÑÏ≤¥ */
.logo-wrapper {
  flex: 1;  
  display: flex;
  text-align: center;
}

/* Î°úÍ≥† Î∞∞Í≤Ω */
.logo-background {
  width: 300px;
  object-fit: cover;
  margin-left: -55px;
  margin-top: 0px;
}

/* Î°úÍ≥† ÌÖçÏä§Ìä∏ */
.logo-text {
  position: absolute;
  top: 20px;
  left: 110px;
  transform: translate(-50%, -50%);
  font-size: 20px;
  color: rgb(0, 0, 0);
  font-weight: 800;
  white-space: nowrap;
}

/* ÌñÑÎ≤ÑÍ±∞ ÏïÑÏù¥ÏΩò (Ï¥àÍ∏∞ Ïà®ÍπÄ ‚Üí Î™®Î∞îÏùºÏóêÏÑúÎßå Î≥¥ÏûÑ) */
.menu-icon {
  flex: 1;
  text-align: right;
  font-size: 30px;
  cursor: pointer;
  display: none;
}

/* Î©îÎâ¥ Ï†ÑÏ≤¥ */
.header-nav-menu {
  flex: 1; /* Í∞ÄÏö¥Îç∞Î°ú Ï†ïÎ†¨ ÏâΩÍ≤å ÎßåÎì§Í∏∞ ÏúÑÌï¥ */
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);  
  display:flex; 
  align-items:center; /* ÏàòÏßÅ Ï§ëÏïô Ï†ïÎ†¨ */
  justify-content: center;  /* üëâ Ï§ëÏïôÏ†ïÎ†¨ */
  white-space: nowrap; /* Ï§Ñ Î∞îÍøà Î∞©ÏßÄ*/
  overflow: hidden;
  position: absolute;
  font-weight: 800;
}

/* Î©îÎâ¥Îßå */
.nav-menu-container {
  display: flex;
}

/* Î©îÎâ¥ Í∞ÅÍ∞Å */
.nav-menu-content {
  flex-shrink: 1;
  margin: 0 40px;
  font-weight: 500;
  font-size: 15px;
  cursor: pointer; /* üëâ Ïù¥Í±∞ Ï∂îÍ∞Ä */
}
  /* Î©îÎâ¥ Ìò∏Î≤Ñ */
  .nav-menu-content:hover {
      color: #6841ea;;
      transition: color 0.3s;
  }


/* Î∞òÏùëÌòï Î™®Î∞îÏùº Ïä§ÌÉÄÏùº Ï†ÅÏö© */
@media (max-width: 768px) {

  /* ÌñÑÎ≤ÑÍ±∞ ÏïÑÏù¥ÏΩò */
  .menu-icon {
    display: block;
  }
  /* Î°úÍ≥† Î∂ÄÎ∂Ñ */
  .logo-text {
    font-family: 'Freesentation', sans-serif; /* ‚úÖ Ìè∞Ìä∏ Ï†ÅÏö© */
    font-weight: 700;
  }  

  /* Î©îÎâ¥ Ï†ÑÏ≤¥ */
  .header-nav-menu {
    display: none;
    position: absolute;
    top: 90%;
    left: 50%;
    transform: translateX(-50%);
    font-family: 'Freesentation', sans-serif;
    flex-direction: row;
    justify-content: center;
    overflow-x: auto;
    box-sizing: border-box;
    z-index: 999;
    white-space: nowrap;
  }
    @keyframes slideInRightMenu {
      from { opacity: 0; }
      to { opacity: 1; }
    }

  /* Ïò§Ìîà ÌñàÏùÑÎïå */
  .header-nav-menu.open {
    display: flex;
    align-items: flex-start;
    animation: slideInRightMenu 1.0s ease forwards;
  }

  .nav-menu-container {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
  }  
  
  /* Î©îÎâ¥ Í∞ÅÍ∞Å */
  .nav-menu-content {
    flex-shrink: 1;
    font-size: 2.5vw;
    text-align: center;
    border-radius: 10px;  /* ‚Üê Ïù¥ Ìïú Ï§Ñ Ï∂îÍ∞Ä */
    background-color: #FA7000;
    color: #ffffff;
    padding: 10px;
    margin: 0 8px;
    font-weight: 800;
  }
}