body {
  overflow-x: hidden;
}

  @keyframes slideInLeft {
    from {
      transform: translateX(100%);
      opacity: 0;
    }
    to {
      transform: translateX(0);
      opacity: 1;
    }
  }

  @keyframes slideInRight {
    from {
      transform: translateX(-100%);
      opacity: 0;
    }
    to {
      transform: translateX(0);
      opacity: 1;
    }
  }

/* Î©îÏù∏ Ï†ÑÏ≤¥ */
.main_container {
  justify-content: center;
}

/* Î©îÏù∏ Î∞∞Í≤Ω Ïù¥ÎØ∏ÏßÄ */
.main_image {
  position: relative;
  margin: auto;
  display: block;
  width: 81%;
}
  .main_image.animate {
    animation: slideInLeft 1s ease-out forwards;
  }
    
/* Î©îÏù∏ Í∏ÄÏî® */
.overlay_text {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  pointer-events: none; /* ÎßàÏö∞Ïä§ Ïïà ÎßâÍ≤å Ï≤òÎ¶¨ */
}

/* Î©îÏù∏ ÏúÑ ÌÅ∞ Í∏ÄÏî®*/
  .overlay_image_1 {
  position: absolute;
  top: 16.4%;
  left: 10.6%;
  width: 44.2vw;
  height: auto; /* ÏÑ∏Î°ú ÎπÑÏú® Ïú†ÏßÄ */
  }

/* Î©îÏù∏ ÏúÑ ÏûëÏùÄ Í∏ÄÏî®*/
.overlay_image_2 {
  position: absolute;
  top: 79.3%;
  left: 12.5%;
  width: 15.4vw;
  height: auto;
}
  .overlay_image_1.animate,
  .overlay_image_2.animate {
    animation: slideInRight 1s ease-out forwards;
  }

/* ÍπÉÌóàÎ∏å Î°úÍ≥† */
.social-sidebar {
  position: fixed;
  top: 85%;
  right: 0;
  display: flex;
  flex-direction: column;
  gap: 15px;
  padding: 10px 5px;
}
  .social-sidebar a img {
    width: 40px;
    height: 40px;
    transition: transform 0.2s ease;
  }
  .social-sidebar a img:hover {
    transform: scale(1.1);
  }

/* Îß®ÏúÑÎ°ú Ïò¨Î¶¨Îäî Ïä§ÌÅ¨Î°§ ÏïÑÏù¥ÏΩò */
.scroll-top {
  width: 50px;
  height: 50px;
  color: #FA7000;
  font-size: 24px;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  transition: transform 0.2s ease;
}

.scroll-to-top:hover {
  transform: scale(1.1);
}  


/* üì± Î™®Î∞îÏùº ÎåÄÏùë (Î∞òÏùëÌòï Î∂ÄÎ∂Ñ Ï∂îÍ∞Ä) */
@media (max-width: 768px) {
  
  body {
    overflow-x: hidden;
  }
  
  /* Î©îÏù∏ Ï†ÑÏ≤¥ */
  .main_container {
    height: 88vh;
  }

  /* Î©îÏù∏ Î∞∞Í≤Ω Ïù¥ÎØ∏ÏßÄ */
  .main_image {
    position: relative;
    top : 13%;
    right: 130.5%;
    margin: auto;
    display: block;
    width: 230%;
    height: auto;
  }

  /* Î©îÏù∏ Í∏ÄÏî® */
  .overlay_text {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    pointer-events: none; /* ÎßàÏö∞Ïä§ Ïïà ÎßâÍ≤å Ï≤òÎ¶¨ */
    inset: 0;
  }

  /* Î©îÏù∏ ÏúÑ ÌÅ∞ Í∏ÄÏî®*/
  .overlay_image_1 {
    position: absolute;
    top: 5%;
    left: 0%;
    width: 90%;
  }
 
  /* Î©îÏù∏ ÏúÑ ÏûëÏùÄ Í∏ÄÏî®*/
  .overlay_image_2 {
    position: absolute;
    top: 20%;
    left: 1.9%;
    width: 30%;
  }

  /* ÍπÉÌóàÎ∏å Î°úÍ≥† */
  .social-sidebar {
    right: 20px;
  }

  .social-sidebar a img {
    width: 30px;
    height: 30px;
  }

  /* Îß®ÏúÑÎ°ú Ïò¨Î¶¨Îäî Ïä§ÌÅ¨Î°§ ÏïÑÏù¥ÏΩò */
  .scroll-top {
    width: 40px;
    height: 40px;
    font-size: 20px;
  }
}